<template>
	<div class="tabs-container">
		<div class="tabs-wrap">
			<el-tabs value="farmIntro" @tab-click="handleTabs">
				<el-tab-pane label="åŸºåœ°ä»‹ç»" name="farmIntro">
					<div class="farm-intro" v-if="generateFarmIntro">
						<div class="farm-intro-container" ref="farmIntroContainer">
							<div class="farm-intro-wrap">
								<p class="info-item"><b>åŸºåœ°ç®€ä»‹ï¼š</b>{{ generateFarmIntro.farmIntro.farmDesc }}</p>
								<p class="info-item" v-if="!!businessCategoryStr"><b>ç»è¥ç±»ç›®ï¼š</b>{{ businessCategoryStr || '' }}</p>
								<p class="info-item"><b>ç»è¥ä¸»ä½“ï¼š</b>{{ generateFarmIntro.companyInfo.companyName }}</p>
								<p class="info-item">
									<b>ä¸»ä½“ç±»å‹ï¼š</b
									>{{ generateFarmIntro.companyInfo && generateFarmIntro.companyInfo.companyCategoryVO && generateFarmIntro.companyInfo.companyCategoryVO.companyCategoryName }}
								</p>
								<p class="info-item"><b>æ³•äººä»£è¡¨ï¼ˆè´Ÿè´£äººï¼‰ï¼š</b>{{ generateFarmIntro.companyInfo.legalPerson }}</p>
								<p class="info-item"><b>è”ç³»æ–¹å¼ï¼š</b>{{ generateFarmIntro.companyInfo.legalPersonPhone }}</p>
								<p class="info-item clearfix">
									<span>
										<b>ä¸»ä½“å”¯ä¸€æ ‡è¯†ï¼š</b><i style="font-style: normal;">{{ generateFarmIntro.companyInfo.enterpriseCreditCode }}</i>
									</span>
								</p>
								<p class="info-item clearfix" v-if="generateFarmIntro.farmIntro.landRightStartDate || generateFarmIntro.farmIntro.landRightEndDate">
									<span>
										<b>åœŸåœ°æ‰¿åŒ…æœŸé™ï¼š</b>
										<i style="font-style: normal;" v-if="generateFarmIntro.farmIntro.landRightStartDate">
											{{ generateFarmIntro.farmIntro.landRightStartDate | dateFormat('yyyy.MM.dd') }}~{{
												generateFarmIntro.farmIntro.landRightEndDate | dateFormat('yyyy.MM.dd')
											}}
										</i>
									</span>
									<span class="icon-pic-wrap f-right" v-if="generateFarmIntro.farmIntro.landRightImgUrl" @click="showCompanyIcon = true">
										<i class="iconfont icon-pic"></i>
									</span>
								</p>
								<p class="info-item">
									<b>åŸºåœ°ä½ç½®ä¸è§„æ¨¡ï¼š</b>åŸºåœ°ä½äº{{ generateFarmIntro.farmIntro.cityName }}{{ generateFarmIntro.farmIntro.countyName }}{{ generateFarmIntro.farmIntro.farmAddress }}
									<span v-if="generateFarmIntro.farmIntro.farmAcre"
										>ï¼Œå åœ°{{ generateFarmIntro.farmIntro.farmAcre | toFloat }}{{ generateFarmIntro.farmIntro.farmAcreUnit | areaUnit }}ã€‚</span
									>
								</p>
								<div class="info-item">
									<div class="head-item">
										<b>æ£€æµ‹ä¸è®¤è¯</b>
										<i v-if="checkAuthCount">å…±{{ checkAuthCount }}ä»½</i>
										<i v-else>æ— </i>
									</div>
									<div class="content-item" v-if="farminfo.inspectVOS.length">
										<ul class="content-lists">
											<li class="list" v-if="farminfo.inspectVOS.length" v-for="(item, index) in farminfo.inspectVOS" :key="index">
												<el-tooltip class="item" effect="dark" :content="item.reportName" placement="top-start">
													<p class="list-name">{{ item.reportName }}</p>
												</el-tooltip>
												<span class="list-line"></span>
												<p class="list-des" @click="openViews(item.inspectReportImages)">
													<i class="iconfont icon-pic"></i>
													{{ item.inspectReportImages.length }}å¼ 
												</p>
											</li>
										</ul>
									</div>
								</div>
							</div>
						</div>
					</div>
				</el-tab-pane>
				<el-tab-pane label="å†œäº‹æ“ä½œ" name="input">
					<div class="farmword-container" v-if="generateFarmIntro">
						<div ref="farmRecordScroll" class="farmword-wrap">
							<farm-record :companyId="farminfo.companyVO.id" :farmId="farmId"></farm-record>
						</div>
					</div>
				</el-tab-pane>
			</el-tabs>
		</div>
		<views :pics="[generateFarmIntro.farmIntro.landRightImgUrl || '']" v-if="showCompanyIcon" @viewerListen="showCompanyIcon = false"></views>
		<views :pics="viewPics" v-if="showViews" @viewerListen="showViews = false"></views>
		<!--æœªæ¥é¢„è®¡äº§é‡-->
		<estimate-yield-model
			v-if="showEstimateYieldModel"
			:show-model.sync="showEstimateYieldModel"
			:goods-subject-unit="estimateGoodsSubjectUnit"
			:estimate-yield-subject-model="estimateYieldSubjectModel"
			:goods-subject-list-model="estimateGoodsSubjectList"
		></estimate-yield-model>
	</div>
</template>

<script>
import {deepCopy, _initBScroll, dateFormat, maxDecimalPlaces, unitTranslate} from '@/common/fn';
import farmRecord from '@/components/plotlist/farm-record';
// å›¾ç‰‡æŸ¥çœ‹
import views from '@/components/common/viewPics';
import estimateYieldModel from '@/components/farmdetail/estimateYieldModel';
import farmIncome from '@/common/data/jiujiang-data.js'; // ä¹æ±Ÿ æµ‹è¯•æœæ­£å¼æœ éœ€ç”¨çš„å‡æ•°æ®
const color = ['#F0C87B', '#A6D889', '#DD90CB', '#9173FE', '#4DCBFF', '#1DE9B6', '#ca8622', '#bda29a', '#6e7074', '#546570', '#c4ccd3'];
const sales = [];
const xData = ['ä¸€æœˆ', 'äºŒæœˆ', 'ä¸‰æœˆ', 'å››æœˆ', 'äº”æœˆ', 'å…­æœˆ'];
const data = [220, 182, 191, 234, 290, 330];
const barData = xData.map((item, index) => {
	return {
		name: item,
		value: data[index],
		itemStyle: {
			color: color[index]
		}
	};
});
const barOption = {
	tooltip: {
		show: true,
		trigger: 'item',
		textStyle: {
			fontSize: 12,
			color: '#ffffff'
		}
	},
	title: {
		text: 'è¿‘åŠå¹´é”€å”®é¢',
		textStyle: {
			color: '#02E4EE',
			fontSize: 16,
			fontWeight: 'bold'
		},
		subtextStyle: {
			color: '#6DC1CB',
			fontSize: 12,
			fontWeight: 400
		},
		left: 'center',
		padding: [17, 0, 0, 0],
		itemGap: 10,
		textVerticalAlign: 'top' // subtext: 'å•ä½ï¼šä¸‡å…ƒ'
	},
	grid: {
		show: true,
		top: 66,
		right: 24,
		height: 130,
		width: 270,
		borderColor: '#0D394A'
	},
	color: color,
	xAxis: {
		data: xData,
		scale: true,
		axisLabel: {
			textStyle: {
				color: '#6DC1CB',
				fontSize: 12
			},
			align: 'center'
		},
		axisTick: {
			show: false
		},
		axisLine: {
			show: true,
			lineStyle: {
				color: '#0D394A'
			}
		},
		z: 10
	},
	yAxis: {
		nameTextStyle: {
			fontSize: 12,
			color: '#6DC1CB',
			padding: [0, 0, 0, 0]
		},
		axisLine: {
			show: false,
			lineStyle: {
				color: '#0D394A'
			}
		},
		axisTick: {
			show: false
		},
		splitLine: {
			show: true,
			lineStyle: {
				color: '#0D394A'
			}
		},
		axisLabel: {
			textStyle: {
				color: '#6DC1CB',
				fontSize: 12
			}
		}
	},
	series: [
		{
			type: 'bar',
			barWidth: 24,
			data: barData
		}
	]
};
/* å†—ä½™ä»£ç åç­‰æµ‹è¯•æ‰¹å‡†åˆ é™¤ğŸ”ª */
const _farmIncome = (() => {
	const _o = {};
	for (const [key, value] of Object.entries(farmIncome)) {
		const o = {};
		value.forEach(item => {
			o[item.incomeDateStr] = item;
		});
		_o[key] = o;
	}
	return _o;
})();

export default {
	name: 'farmDetailsTabs',
	data() {
		return {
			// é”€å”®é¢
			sales: null,
			salesBar: deepCopy(barOption), // äº§é‡
			yield: null,
			yieldBar: deepCopy(barOption),
			farmRecordList: [],
			estimateYield: null,
			estimateYieldBar: deepCopy(barOption),
			generateFarmIntro: {
				// åŸºåœ°ä¿¡æ¯
				farmIntro: {}, // å…¬å¸ä¿¡æ¯
				companyInfo: {}, // åŸºåœ°è¿‡å»å…­ä¸ªæœˆçš„æ”¶å…¥æ€»è®¡
				farmIncomeVOS: [], // åŸºåœ°è¿‡å»å…­ä¸ªæœˆçš„é‡‡æ”¶æ€»è®¡
				govPickVOList: [],
				businessCategoryList: []
			},
			farmIntroContainer: null,
			farmRecordScroll: null,
			showCompanyIcon: false,
			viewPics: [],
			showViews: false,
			goodsSubjectUnit: null,
			estimateGoodsSubjectUnit: null, // å®é™…äº§å“å¤§ç±»åˆ—è¡¨
			goodsSubjectList: [
				/* { name: 'å†œä¸š', id: 'agri' }, { name: 'æ—ä¸š', id: 'forestry' }, { name: 'ç•œç‰§ä¸š', id: 'animal' }, { name: 'æ¸”ä¸š', id: 'fishery' } */
			],
			estimateGoodsSubjectList: [],
			yieldSubject: 'agri',
			estimateYieldSubject: 'agri',
			estimateYieldSubjectModel: 'agri',
			showEstimateYieldModel: false,
			curDate: null // ç»è¥ä¸»ä½“ better-scrollå®ä¾‹
		};
	},
	props: ['farminfo', 'farmId'],
	computed: {
		businessCategoryStr() {
			//å†œåœºç»è¥ç±»ç›®
			if (this.generateFarmIntro.businessCategoryList.length && this.generateFarmIntro.farmIntro.businessCategoryIdList && this.generateFarmIntro.farmIntro.businessCategoryIdList.length) {
				var nowFarmCateList = this.generateFarmIntro.farmIntro.businessCategoryIdList;
				var catelist = this.generateFarmIntro.businessCategoryList;
				var catenames = [];
				catelist.map(cate => {
					nowFarmCateList.includes(cate['id']) && catenames.push(cate['bizCategoryName']);
				});
				return catenames.join('ï¼Œ');
			} else {
				return '';
			}
		}
	},
	created() {
		this.initData();
	},
	async mounted() {
		await this.getCropSubject(); // tab1 è·å–å®é™…ä½œç‰©å¤§ç±»åˆ—è¡¨
		this.$nextTick(async () => {
			this.farmIntroContainer = _initBScroll(this.$refs.farmIntroContainer, {
				scrollX: false,
				freeScroll: false,
				click: false
			});
			this.farmRecordScroll = _initBScroll(this.$refs.farmRecordScroll, {
				scrollX: false,
				freeScroll: false,
				click: false
			});
		});
	},
	methods: {
		initData() {
			// è·å–åŸºåœ°ä»‹ç»
			this.generateFarmIntro = {
				// åŸºåœ°ä¿¡æ¯
				farmIntro: this.farminfo.farmDetailVO, // å…¬å¸ä¿¡æ¯
				companyInfo: this.farminfo.companyVO || {}, // åŸºåœ°è¿‡å»å…­ä¸ªæœˆçš„æ”¶å…¥æ€»è®¡
				farmIncomeVOS: this.farminfo.farmIncomeVOS, // åŸºåœ°è¿‡å»å…­ä¸ªæœˆçš„é‡‡æ”¶æ€»è®¡
				govPickVOList: this.farminfo.govPickVOList,
				businessCategoryList: this.farminfo.businessCategoryList
			};
			this.checkAuthCount = this.farminfo.inspectVOS.length; // æ£€æµ‹è®¤è¯æ•°é‡
			// å†œäº‹è®°å½• åˆ—è¡¨
		},

		// ç›‘æµ‹å¤§ç±»æ˜¯å¦éœ€è¦éœ€è¦æ¢ç®—å•ä½
		checkSubjectChangeUnit(subject) {
			const mapping = {
				agri: true,
				forestry: false,
				animal: false,
				fishery: true
			};
			return mapping[subject];
		},
		yieldXAxis(mode, format = 'yyyy.MM') {
			const curDateTimestamp = new Date(this.curDate).setDate(1);
			let curM = new Date(curDateTimestamp).getMonth();
			const xAxis = [];
			for (let i = 0; i < 6; i++) {
				xAxis.push(mode === 'add' ? dateFormat(new Date(curDateTimestamp).setMonth(curM++), format) : dateFormat(new Date(curDateTimestamp).setMonth(--curM), format));
			}
			// console.log(dateFormat(curDate.setMonth(11), 'yyyy.MM'));
			return xAxis;
		},

		// è·å–å®é™…ä½œç‰©å¤§ç±»åˆ—è¡¨
		async getCropSubject() {
			return this.$ajax
				.post(this.$api.public.farmCropSubject, {
					farmId: this.farmId
				})
				.then(({data: res}) => {
					this.curDate = res.curDate;
					if (res.respCode === 0) {
						this.goodsSubjectUnit = {};
						if (res.obj.list.length === 0) return false;
						this.goodsSubjectList = res.obj.list.map(item => {
							this.goodsSubjectUnit[item.cropSubjectIdentifier] = item.cropSubjectIdentifier === 'forestry' || item.cropSubjectIdentifier === 'animal' ? item.cropCategoryUnitAll : 'å¨';
							return {
								name: item.cropSubjectNickname,
								id: item.cropSubjectIdentifier
							};
						});
						this.yieldSubject = res.obj.list[0].cropSubjectIdentifier;
						return true;
					}
				});
		},

		// è·å–é¢„è®¡ä½œç‰©å¤§ç±»åˆ—è¡¨
		getEstimateCropSubject() {
			return new Promise((resolve, reject) => {
				this.$ajax
					.post(this.$api.public.getEstimateFarmCropSubject, {
						farmId: this.farmId
					})
					.then(({data: res}) => {
						if (res.respCode === 0) {
							resolve(res.obj);
						}
					})
					.catch(err => {
						console.log(err);
						reject(err);
					});
			});
		},

		// æœªæ¥é¢„è®¡äº§é‡
		async processEstimateCropSubject() {
			const res = await this.getEstimateCropSubject();
			this.estimateGoodsSubjectUnit = {};
			if (res.list.length === 0) return false;
			this.estimateGoodsSubjectList = res.list.map(item => {
				this.estimateGoodsSubjectUnit[item.cropSubjectIdentifier] = item.cropSubjectIdentifier === 'forestry' || item.cropSubjectIdentifier === 'animal' ? item.cropCategoryUnitAll : 'å¨';
				return {
					name: item.cropSubjectNickname,
					id: item.cropSubjectIdentifier
				};
			});
			this.estimateYieldSubject = res.list[0].cropSubjectIdentifier;
		},

		// è·å–è¿‘åŠå¹´çš„äº§é‡
		getOutput(subjectIdentifier) {
			return this.$ajax
				.post(this.$api.farmDetail.getOutputListByFarmId, {
					dateList: this.yieldXAxis('minus', 'yyyy-MM-dd').reverse(),
					farmId: this.farmId,
					subjectIdentifier
				})
				.then(({data: res}) => {
					if (res.respCode === 0) {
						return res.obj;
					}
				});
		},

		// è·å–æœªæ¥é¢„è®¡äº§é‡æ•°æ®è¡¨
		getEstimateYieldEchartsData() {
			return new Promise((resolve, reject) => {
				this.$ajax
					.post(this.$api.farmDetail.getPickForecastMonthByFarmId, {
						farmId: this.$route.params.farmid,
						subjectIdentifier: this.estimateYieldSubject
					})
					.then(({data: res}) => {
						if (res.respCode === 0) {
							resolve(res.obj);
						}
					})
					.catch(err => {
						console.log(err);
						reject(new Error(err));
					});
			});
		},

		// è¿‘åŠå¹´é”€å”®é¢
		loadSalesBar() {
			const unit = 'ä¸‡å…ƒ';
			const fakeData = _farmIncome[this.generateFarmIntro.farmIntro.farmId];
			this.salesBar.title.text = 'è¿‘åŠå¹´é”€å”®é¢';
			this.salesBar.title.subtext = `å•ä½ï¼š${unit}`;
			this.salesBar.xAxis.data = this.generateFarmIntro.farmIncomeVOS.map(item => {
				return dateFormat(item.incomeDate, 'yyyy.MM');
			});
			this.salesBar.xAxis.axisLabel.formatter = v => v; // `${v.split('.')[1]}\n${v.split('.')[0]}`;
			this.salesBar.series[0].data = this.generateFarmIntro.farmIncomeVOS.map((item, index) => {
				return {
					name: dateFormat(item.incomeDate, 'yyyy.MM'),
					value: (() => {
						if (fakeData) {
							return fakeData[item.incomeDateStr]
								? maxDecimalPlaces({
										value: fakeData[item.incomeDateStr].totalAmount / Math.pow(10, 4)
								  })
								: 0;
						}
						return maxDecimalPlaces({
							value: item.totalAmount / Math.pow(10, 4)
						});
					})(),
					itemStyle: {
						color: color[index]
					}
				};
			});
			this.salesBar.tooltip.formatter = v =>
				`${v.name}<br/>${
					unitTranslate({
						value: v.value,
						unit: 'å…ƒ',
						base: 10000
					}).formatter
				}`;
			this.salesBar.yAxis.axisLabel.formatter = v => unitTranslate({value: v}).formatter;
			this.sales = this.$echarts.init(this.$refs.sales);
			this.sales.setOption(this.salesBar);
		},

		// åˆå§‹åŒ–å®é™…äº§é‡
		async loadYieldBar(subjectIdentifier) {
			const unit = this.goodsSubjectUnit[subjectIdentifier] || 'å¨';
			const res = await this.getOutput(subjectIdentifier);
			this.yieldBar.title.text = 'è¿‘åŠå¹´å®é™…äº§é‡';
			this.yieldBar.xAxis.data = res.map(item => {
				return dateFormat(item.pickTime, 'yyyy.MM');
			});
			this.yieldBar.yAxis.name = `å•ä½ï¼š${unit}`;
			this.yieldBar.xAxis.axisLabel.formatter = v => v;
			this.yieldBar.series[0].data = res.map((item, index) => {
				return {
					name: dateFormat(item.pickTime, 'yyyy.MM'),
					value: maxDecimalPlaces({value: this.checkSubjectChangeUnit(subjectIdentifier) ? item.sumPickOutput / Math.pow(10, 3) : item.sumPickOutput}), // å…¬æ–¤ - å¨
					itemStyle: {
						color: color[index]
					}
				};
			});
			this.yieldBar.yAxis.axisLabel.formatter = v => unitTranslate({value: v}).formatter;
			this.yieldBar.tooltip.formatter = v =>
				`${v.name}<br/>${
					unitTranslate({
						value: v.value,
						unit
					}).formatter
				}`;
			// console.log(this.$refs.yield);
			this.yield = this.$echarts.init(this.$refs.yield);
			this.yield.setOption(this.yieldBar);
		},

		// åˆå§‹åŒ–æœªæ¥é¢„è®¡äº§é‡
		async loadEstimateYieldEchart(subjectIdentifier) {
			const res = await this.getEstimateYieldEchartsData();
			const unit = this.estimateGoodsSubjectUnit[subjectIdentifier] || 'å¨';
			this.estimateYieldBar.title.text = 'æœªæ¥é¢„è®¡äº§é‡';
			this.estimateYieldBar.xAxis.data = res.total.map(item => {
				return dateFormat(new Date(`${item.pickMonthStr}-01`), 'yyyy.MM');
			});
			this.estimateYieldBar.series[0].data = res.total.map((item, index) => {
				return {
					name: dateFormat(new Date(`${item.pickMonthStr}-01`), 'yyyy.MM'),
					value: maxDecimalPlaces({value: this.checkSubjectChangeUnit(subjectIdentifier) ? item.sumTotal / Math.pow(10, 3) : item.sumTotal}), // å…¬æ–¤ - å¨
					itemStyle: {
						color: color[index]
					}
				};
			});
			this.estimateYieldBar.yAxis.name = `å•ä½ï¼š${unit}`;
			this.estimateYieldBar.xAxis.axisLabel.formatter = v => v;
			this.estimateYieldBar.yAxis.axisLabel.formatter = v => unitTranslate({value: v}).formatter;
			this.estimateYieldBar.tooltip.formatter = v =>
				`${v.name}<br/>${
					unitTranslate({
						value: v.value,
						unit
					}).formatter
				}`;
			this.estimateYield = this.$echarts.init(this.$refs.estimateYield);
			this.estimateYield.setOption(this.estimateYieldBar);
			// æ£€æµ‹ä¸è®¤è¯è¶‹åŠ¿å›¾clickäº‹ä»¶
			this.estimateYield.on('click', r => {
				// è§¦å‘çˆ¶çº§å…³é—­å¼¹æ¡†ç­‰äº¤äº’
				this.$emit('closeAllDialogEv');
				setTimeout(() => {
					// é˜²æ­¢å¼¹æ¡†é‡å çš„æƒ…å†µ
					this.estimateYieldSubjectModel = this.estimateYieldSubject;
					this.showEstimateYieldModel = true;
				}, 200);
			});
		},
		handleTabs(tar) {
			this.$nextTick(() => {
				if (tar.name === 'input') {
					this.farmRecordScroll.refresh();
				}
			});
		},
		openViews(p) {
			this.showViews = true;
			this.viewPics = p;
		}
	},
	components: {
		farmRecord,
		views,
		estimateYieldModel
	}
};
</script>
<style scoped lang="scss">
@import '~styles/selectSkin_default';
@import '~styles/tabsSkin_default';
@import '~styles/scrollbarSkin_default';
@import '~styles/tooltipSkin_default';
@import '~styles/farmdetails/farmdetailstabs';
</style>



